import {Component, OnInit} from '@angular/core';
import {ServicesService} from '../services.service';
import {Router} from '@angular/router';

declare var $: any;

@Component({
    selector: 'app-edit-service',
    templateUrl: './edit-service.component.html',
    styleUrls: ['./edit-service.component.scss']
})
export class EditServiceComponent implements OnInit {

    serviceName: string;
    servicePrice: string;
    serviceDuration: number;

    constructor(private servicesService: ServicesService, private router: Router) {
    }

    ngOnInit() {
 if (this.servicesService.serviceId === undefined){
     this.router.navigate(['service']);

 }

        this.serviceName = this.servicesService.name;
        this.servicePrice = this.servicesService.servicePrice;
        this.serviceDuration = parseInt(this.servicesService.serviceDuration, 0);
    }


    public getEditService() {

        const servicetypeId: any = $('input[name="service_type"]').val(),
            price: any = $('input[name="price"]').val(),
            duration: any = $('input[name="duration"]').val(),
            data: object = {
                'name': servicetypeId,
                'price': price,
                'duration': duration
            };
        this.servicesService.sendEditService(data).subscribe((res: any) => {
            this.router.navigate(['service']);

        }, (err: any) => {
            console.log(err);
        });
    }

    public deleteServiceEvent () {
        this.servicesService.deleteService().subscribe((res: any) => {
            this.router.navigate(['service']);
            console.log(res);

        }, (err: any) => {
            console.log(err);
        });
    }
}



