import { Component, OnInit } from '@angular/core';
import {ClientsService} from '../clients.service';
import {Router} from '@angular/router';

declare var $: any;

@Component({
  selector: 'app-clients-add',
  templateUrl: './clients-add.component.html',
  styleUrls: ['./clients-add.component.scss']
})
export class ClientsAddComponent implements OnInit {
    errorMessage: any;
  constructor(private clientsService: ClientsService, private router: Router ) { }

  ngOnInit() {
  }

    public getNewClientData (e) {


        e.preventDefault();
        const $userFullName: string = e.target.elements[1].value,
            $firstName: string = $userFullName.substr(0, $userFullName.indexOf(' ')),
            $lastName: string = $userFullName.substr($userFullName.indexOf(' ') + 1),
            userPhone: string = e.target.elements[2].value;

        const usersData: object = {
            'salonId': localStorage.stylistPlanerCurrentSalonId,
            'userId': localStorage.stylistPlanerCurrentUserId,
            'phone': userPhone,
            'firstName': $.trim($firstName),
            'lastName': $.trim($lastName),
        };
        this.clientsService.putNewClient(usersData)
            .subscribe(
                (data: any) => {
                    this.router.navigate(['clients']);
                },
                err => {
                    this.errorMessage = err.error.message;

                    console.log(err);
                });

        return false;
    }
}



