import {Component, OnInit} from '@angular/core';
import {ClientsService} from '../clients.service';
import {Router} from '@angular/router';

@Component({
    selector: 'app-client-details',
    templateUrl: './client-details.component.html',
    styleUrls: ['./client-details.component.scss']
})
export class ClientDetailsComponent implements OnInit {

    firstName: any;
    lastName: any;
    phone: any;
    nextAppointment: any;
    serviceName: any;
    previousAppointment: any;
    orderId: any;
    success: any;
    addDataError: any;

    constructor(private clientsService: ClientsService, private router: Router) {
    }

    ngOnInit() {
        this.clientsService.viewClientDetails().subscribe((data: any) => {
            this.firstName = data.firstName;
            this.orderId = data.orderId;
            this.lastName = data.lastName;
            this.phone = data.phone;
            this.nextAppointment = this.clientsService.nextAppointment;
            this.serviceName = this.clientsService.serviceName;
        });


        this.clientsService.getCustomerOrder().subscribe((data: any) => {
            this.previousAppointment = data;
        });

    }


    sendReminder() {
        this.clientsService.getReminder(this.orderId).subscribe(
            (data: any) => {
                this.success = data.messageId;
            }, (err: any) => {
                this.addDataError = err.error.message;
            });
    }

    clientEdit() {
        this.clientsService.clientFirstName = this.firstName;
        this.clientsService.clientLastName = this.lastName;
        this.clientsService.clientPhone = this.phone;
        this.router.navigate(['clients/edit']);
    }
}



