import {Component, OnInit} from '@angular/core';
import {ProfileService} from '../profile/profile.service';
import {Router} from '@angular/router';

declare var $: any;

@Component({
    selector: 'app-nav',
    templateUrl: './nav.component.html',
    styleUrls: ['./nav.component.scss']
})
export class NavComponent implements OnInit {
    slylistLogin: any;
    adminLogin: any;

    constructor(private profileService: ProfileService,
                private router: Router) {
    }

    ngOnInit() {
        this.slylistLogin = localStorage.stylistPlanerCurrentRole == 2;
        this.adminLogin = localStorage.stylistPlanerCurrentRole == 1;
    }


    signOut() {
        localStorage.removeItem('stylistPlanerCurrentUserId');
        localStorage.removeItem('stylistPlanerCurrentSalonId');
        localStorage.removeItem('stylistPlanerCurrentRole');
        localStorage.removeItem('stylistPlanerCurrentRoleName');
    }

    closeNav() {
        $('.navbar-toggler').click();
    }

    userIdReset() {
        this.profileService.userId = undefined;
    }


}



