import {Component, OnInit} from '@angular/core';
import {ClientsService} from '../clients.service';
import {Router} from '@angular/router';

declare var $: any;

@Component({
    selector: 'app-client-edit',
    templateUrl: './client-edit.component.html',
    styleUrls: ['./client-edit.component.scss']
})
export class ClientEditComponent implements OnInit {
    firstName: any;
    lastName: any;
    phone: any;
    dataError: any;

    constructor(private clientsService: ClientsService, private router: Router) {
    }

    ngOnInit() {
        this.firstName = this.clientsService.clientFirstName;
        this.lastName = this.clientsService.clientLastName;
        this.phone = this.clientsService.clientPhone;
        if (this.firstName === undefined && this.lastName === undefined && this.phone === undefined) {
            this.router.navigate(['clients']);
        }
    }


    public getEditClientData(e) {
        e.preventDefault();
        let $userFullName: string = $.trim(e.target.elements[1].value),
            $firstName: string = $userFullName.substr(0, $userFullName.indexOf(' ')),
            $lastName: string = $userFullName.substr($userFullName.indexOf(' ') + 1);


        const editData: any = {
            'salonId': localStorage.stylistPlanerCurrentSalonId,
            'phone': e.target.elements[2].value,
            'firstName': $.trim($firstName),
            'lastName': $.trim($lastName),
        };


        this.clientsService.editClientPost(editData).subscribe((data: any) => {
            this.router.navigate(['clients']);

        }, (err: any) => {
            this.dataError = err.error.message;

            console.log(err);

        });
        return false;
    }

}



