import {Component, OnInit, ElementRef} from '@angular/core';
import {CalendarService} from './calendar.service';
import {NgbDateStruct} from '@ng-bootstrap/ng-bootstrap';
import {NgbDateParserFormatter, NgbDatepickerConfig} from '@ng-bootstrap/ng-bootstrap';
// import {DatePipe} from '@angular/common';

declare var $: any;

const now = new Date();

@Component({
    selector: 'app-calendar',
    templateUrl: './calendar.component.html',
    styleUrls: ['./calendar.component.scss'],
    providers: [NgbDatepickerConfig]
})

export class CalendarComponent implements OnInit {

    date: { year: number, month: number };
    model: NgbDateStruct;
    reservationList: Array<any>;
    currentMonthData: any;
    currentDay: number;
    curentMonth: number;
    reservationServices: Array<any>;
    disabledAppointment: boolean;

    constructor(private calendarService: CalendarService,
                private parserFormatter: NgbDateParserFormatter,
                private elementRef: ElementRef,
                private configDate: NgbDatepickerConfig) {
        configDate.outsideDays = 'hidden';

        this.currentDay = this.calendarService.curentDay;
        this.curentMonth = this.calendarService.curentMonth;

        configDate.startDate = { year: now.getFullYear(), month: this.curentMonth};

        this.model = {
            year: now.getFullYear(), month: (now.getMonth() + 1), day: this.currentDay === undefined ? now.getDate() : this.currentDay
        };
    }


    ngOnInit() {
        this.calendarService.curentMonth = this.model.month;

        const cuentDateInit: any = this.currentDay === undefined ? now.getDate() : this.currentDay;
        const curentMonth: any = this.curentMonth === undefined ? this.model.month  : this.curentMonth;

        this.currentMonthData = new Date(now.getFullYear() + '-' + curentMonth + '-' + cuentDateInit);

        this.calendarService.orderListParam = {
            'salonId': localStorage.stylistPlanerCurrentSalonId,
            'shopId': '',
            'customerId': '',
            'userId': localStorage.stylistPlanerCurrentUserId,
            'orderstatusId': 1,
            'dateFrom': now.getFullYear() + '-' + curentMonth + '-' + cuentDateInit,
            'dateTo': now.getFullYear() + '-' + curentMonth + '-' + cuentDateInit,
            'limit': ''
        };


        this.calendarService.getCalendardata(this.model.year + '-' + curentMonth + '-' + this.model.day)

            .subscribe(
                (data: any) => {
                    this.viewCorentDate();
                    this.dateStatus(data);
                    this.disabledAppointment = $('.bg-primary').hasClass('available') || $('.bg-primary').hasClass('filled');
                },
                (err: any) => {
                }
            );


        // this.currentMonthData = $('.custom-select:first-child option:selected').text() + ' ' + this.model.day;

        this.calendarService.getOrderList()
            .subscribe(
                (data: any) => {
                    let reservationService: any;
                    this.reservationList = data;
                    data.forEach(function (eachObj) {
                        reservationService = eachObj.service;
                    });
                    this.reservationServices = reservationService;
                    this.calendarService.reservationServicesId = this.reservationServices;
                    this.calendarService.reservationServicesId = this.reservationServices;
                }
            );


    }

    public getCalendardataInit(e) {


        this.calendarService.getCalendardata(e)
            .subscribe(
                (data: any) => {
                    this.dateStatus(data);
                    this.viewCorentDate();
                    this.disabledAppointment = $('.bg-primary').hasClass('available') || $('.bg-primary').hasClass('filled');
                }
            );
    }


    public dateStatus(dateStatusList) {
        let dateCalendatListDey: any;
        let dateCurentDey: any = this.model.day;

        const dateCalendatList = this.elementRef.nativeElement.querySelectorAll('.ngb-dp-day .btn-light:not(.outside)');

        $('.ngb-dp-day .btn-light:not(.outside)').each(function (index) {
            $(this).removeClass('bg-primary text-white');

            if (index <= dateStatusList.length) {
                $(this).addClass(dateStatusList[index].type);

                if ($(this).text() == dateCurentDey) {
                    $(this).addClass('bg-primary text-white');
                }
            }
        });

        dateCalendatList.forEach(function (eachObj) {
            dateCalendatListDey = eachObj.innerText;
        });
    }

    public getReservation(e) {

        $(e.currentTarget).addClass('disabled');
        setTimeout(function () {
            $('ngb-datepicker').removeClass('disabled');
        }, 1000);


        const curentYear = $(e.currentTarget).find('.custom-select:last-child option:selected').text(),
            curentDayStrindSend = curentYear + '-' + $(e.currentTarget).find('.custom-select:first-child option:selected').val() + '-' + this.model.day;

        this.currentMonthData = new Date(curentDayStrindSend);


        // this.viewCorentDate();
        this.getCalendardataInit(curentDayStrindSend);

        if ($(e.target).hasClass('btn-light')) {
            this.calendarService.orderListParam = {
                'salonId': localStorage.stylistPlanerCurrentSalonId,
                'shopId': '',
                'customerId': '',
                'userId': localStorage.stylistPlanerCurrentUserId,
                'orderstatusId': 1,
                'dateFrom': curentDayStrindSend,
                'dateTo': curentDayStrindSend,
                'limit': ''
            };
        }

        this.calendarService.getOrderList()
            .subscribe(
                (data: any) => {
                    this.innerReservation(data);
                }
            );
    }

    public viewCorentDate() {
        // this.currentMonthData = $('.custom-select:first-child option:selected').text() + ' ' + this.model.day;

    }

    public innerReservation(data) {

        let reservationService: any;
        this.reservationList = data;
        data.forEach(function (eachObj) {
            reservationService = eachObj.service;
        });
        this.reservationServices = reservationService;
        this.calendarService.reservationServicesId = this.reservationServices;
        this.calendarService.reservationServicesId = this.reservationServices;
    }

    editDataAppointment(e) {
        this.calendarService.reservationListToEdit = e;

        this.calendarService.curentDay = this.model.day;
        this.calendarService.curentMonth = parseInt($('.custom-select:first-child option:selected').val(), 10);
    }

    addDataAppointment(e) {
        this.calendarService.curentDay = this.model.day;
        this.calendarService.curentMonth = parseInt($('.custom-select:first-child option:selected').val(), 10);

    }
}



