import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs/Observable';
import {AppService} from '../app.service';


const httpOptions = {
    headers: new HttpHeaders({
        'x-api-key': '9UKaHgvVUu17sIVp0j5LF7tKqjvxQTIu1IkBUZ9y'
    }),
};


@Injectable()
export class CalendarService {

    constructor(private http: HttpClient, private appService: AppService) {
    }

    PIUrl: any = this.appService.globalApiUrl;

    orderEditId: any;
    reservationListToEdit: any;
    orderListParam: any;
    curentDay: any;
    curentMonth: any;
    reservationServicesId: any;
    getCalendardataUrl = '/stylist-scheduling-service/api/schedule?';
    getDayTimeUrl = '/stylist-scheduling-service/api/schedule?';
    getOrderUrl = '/stylist-scheduling-service/api/orders';
    putOrderUrl = '/stylist-scheduling-service/api/orders/add';
    postEditOrderUrl = '/stylist-scheduling-service/api/orders/edit/';
    deleteOrderUrl = '/stylist-scheduling-service/api/orders/cancel/';

    getCalendardata(e): Observable<any> {
        return this.http.get(this.PIUrl + this.getCalendardataUrl + 'userId=' + localStorage.stylistPlanerCurrentUserId + '&salonId=' + localStorage.stylistPlanerCurrentSalonId + '&period=month&date=' + e, httpOptions);
    }

    getClearTime(e, duration): Observable<any> {
        return this.http.get(this.PIUrl + this.getCalendardataUrl + 'userId=' + localStorage.stylistPlanerCurrentUserId + '&salonId=' + localStorage.stylistPlanerCurrentSalonId + '&period=day&date=' + e + '&duration=' + duration, httpOptions);
    }

    getClearTimeEdit(e, duration, orderId): Observable<any> {
        return this.http.get(this.PIUrl + this.getCalendardataUrl + 'userId=' + localStorage.stylistPlanerCurrentUserId + '&salonId=' + localStorage.stylistPlanerCurrentSalonId + '&period=day&date=' + e + '&duration=' + duration + '&orderId=' + orderId, httpOptions);
    }

    getDayTime(e): Observable<any> {
        return this.http.get(this.PIUrl + this.getDayTimeUrl + 'userId=' + localStorage.stylistPlanerCurrentUserId + '&salonId=' + localStorage.stylistPlanerCurrentSalonId + '&period=day&date=' + e, httpOptions);
    }

    getDayTimeEdit(e, orderId): Observable<any> {
        return this.http.get(this.PIUrl + this.getDayTimeUrl + 'userId=' + localStorage.stylistPlanerCurrentUserId + '&salonId=' + localStorage.stylistPlanerCurrentSalonId + '&period=day&date=' + e + '&orderId=' + orderId, httpOptions);
    }

    getOrderList(): Observable<any> {

        const httpOptionsParams = {
            headers: new HttpHeaders({
                'x-api-key': '9UKaHgvVUu17sIVp0j5LF7tKqjvxQTIu1IkBUZ9y'
            }),
            params: this.orderListParam
        };
        return this.http.get(this.PIUrl + this.getOrderUrl, httpOptionsParams);
    }

    addOrder(data): Observable<any> {

        return this.http.put(this.PIUrl + this.putOrderUrl, data, httpOptions);
    }

    postEditOrder(data): Observable<any> {
        return this.http.post(this.PIUrl + this.postEditOrderUrl + this.orderEditId, data, httpOptions);
    }


    deleteOrder(orderId): Observable<any> {
        return this.http.delete(this.PIUrl + this.deleteOrderUrl + orderId, httpOptions);
    }
}



