import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs/Observable';
import {AppService} from '../app.service';


const httpOptions = {
    headers: new HttpHeaders({
        'x-api-key': '9UKaHgvVUu17sIVp0j5LF7tKqjvxQTIu1IkBUZ9y'
    })
};

@Injectable()
export class SignInService {
    constructor(private http: HttpClient, private appService: AppService) {
    }
    PIUrl: any = this.appService.globalApiUrl;

    signInUrl = '/stylist-scheduling-service/api/users/signin';

    putSignInData(signInData): Observable<any> {
        return this.http.post(this.PIUrl + this.signInUrl, signInData , httpOptions);
    }
}





