import { Component, OnInit } from '@angular/core';
import {AdminReservationService} from './admin-reservation.service';

@Component({
  selector: 'app-admin-reservation',
  templateUrl: './admin-reservation.component.html',
  styleUrls: ['./admin-reservation.component.scss']
})
export class AdminReservationComponent implements OnInit {
    orderList: any;

  constructor(private adminReservationService: AdminReservationService) { }

  ngOnInit() {
      this.adminReservationService.getOrderList().subscribe((data: any) => {
          this.orderList = data;
      });

  }

}



