import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs/Observable';
import {Router} from '@angular/router';
import {AppService} from '../app.service';
import {DatePipe} from '@angular/common';


const now = new Date();

const httpOptions = {
    headers: new HttpHeaders({
        'x-api-key': '9UKaHgvVUu17sIVp0j5LF7tKqjvxQTIu1IkBUZ9y'
    })
};

@Injectable()
export class ClientsService {

    customerId: any;
    nextAppointment: any;
    serviceName: any;
    clientFirstName: any;
    clientLastName: any;
    clientPhone: any;

    constructor(private http: HttpClient, private router: Router,
                private datePipe: DatePipe,
                private appService: AppService) {
    }

    PIUrl: any = this.appService.globalApiUrl;


    putNewClientUrl = '/stylist-scheduling-service/api/customers/add';
    getClientsListUrl = '/stylist-scheduling-service/api/customers?salonId=';
    getClientsListAllUrl = '/stylist-scheduling-service/api/customers?userId=';
    viewClientDetailsUrl = '/stylist-scheduling-service/api/customers/view/';
    editClientPostUrl = '/stylist-scheduling-service/api/customers/edit/';
    getCustomerOrderUrl = '/stylist-scheduling-service/api/orders/';
    reminderUrl = '/stylist-scheduling-service/api/orders/reminder/';


    putNewClient(data) {
        return this.http.put(this.PIUrl + this.putNewClientUrl, data, httpOptions);
    }

    getClientsList() {
        return this.http.get(this.PIUrl + this.getClientsListUrl + localStorage.stylistPlanerCurrentSalonId, httpOptions);
    }
    getClientsListAll() {
        return this.http.get(this.PIUrl + this.getClientsListAllUrl + localStorage.stylistPlanerCurrentUserId, httpOptions);
    }

    viewClientDetails() {
        if (this.customerId === undefined) {
            this.router.navigate(['clients']);
            return this.http.get(this.PIUrl + this.viewClientDetailsUrl + localStorage.stylistPlanerCurrentSalonId, httpOptions);

        } else {
            return this.http
                .get(this.PIUrl + this.viewClientDetailsUrl + this.customerId + '?salonId=' + localStorage.stylistPlanerCurrentSalonId, httpOptions);
        }
    }

    editClientPost(data) {
        return this.http.post(this.PIUrl + this.editClientPostUrl + this.customerId, data, httpOptions);
    }

    getCustomerOrder() {
        const year: any = now.getFullYear(),
            monthTo: any = now.getMonth() + 1,
            monthFrom: any = now.getMonth(),
            day: any = now.getDate();


        const dateTo: any = year + '-' + monthTo + '-' + day;
        const dateFrom: any = year + '-' + monthFrom + '-' + day;
        const timeFrom: any = this.datePipe.transform(now, 'HH:mm');

        const httpOptionsCustomerId = {
            headers: new HttpHeaders({
                'x-api-key': '9UKaHgvVUu17sIVp0j5LF7tKqjvxQTIu1IkBUZ9y'
            }),
            params: {
                'salonId': localStorage.stylistPlanerCurrentSalonId,
                'customerId': this.customerId,
                'userId': localStorage.stylistPlanerCurrentUserId,
                // 'dateFrom': '',
                'dateTo': dateTo + ' ' + timeFrom
            }
        };

        return this.http.get(this.PIUrl + this.getCustomerOrderUrl, httpOptionsCustomerId);
    }


    getReminder(orderId) {
        return this.http.get(this.PIUrl + this.reminderUrl +  orderId, httpOptions);
    }

}



