import {Component, ElementRef, OnInit} from '@angular/core';
import {BlackOutTimesService} from './black-out-times.service';
import {NgbDatepickerConfig, NgbDateStruct} from '@ng-bootstrap/ng-bootstrap';
import {CalendarService} from '../calendar/calendar.service';
// import {DatePipe} from '@angular/common';

declare var $: any;
const now = new Date();

@Component({
    selector: 'app-black-out-times',
    templateUrl: './black-out-times.component.html',
    styleUrls: ['./black-out-times.component.scss']
})

export class BlackOutTimesComponent implements OnInit {
    date: { year: number, month: number };
    model: NgbDateStruct;
    blackOutTimelist: any;
    blackOutTime: any;
    currentMonthData: any;
    success: any;
    editDataError: any;
    addDataError: any;
    // timeTest: any;
    constructor(private blackOutTimesService: BlackOutTimesService,
                private calendarService: CalendarService,
                private elementRef: ElementRef,
                // private datePipe: DatePipe,
                private configDate: NgbDatepickerConfig) {
        configDate.outsideDays = 'hidden';
        this.model = {year: now.getFullYear(), month: now.getMonth() + 1, day: now.getDate()};
    }

    ngOnInit() {
        // const cuentDateInit: any = now.getMonth() + 1 + '-' + now.getDate();
        this.getCalendardataInit(now.getFullYear() + '-' + (now.getMonth() + 1) + '-' + now.getDate());
        this.getblackOutTimesInt(now.getFullYear() + '-' + (now.getMonth() + 1) + '-' + now.getDate());
        this.getDayTimeInit(now.getFullYear() + '-' + (now.getMonth() + 1) + '-' + now.getDate());
    }

    public getCalendardataInit(e) {
        this.calendarService.getCalendardata(e)
            .subscribe(
                (data: any) => {
                    this.dateStatus(data);
                    this.viewCorentDate();
                }
            );
    }

    public getDayTimeInit(date) {
        this.calendarService.getDayTime(date)
            .subscribe((data: any) => {
                // data = data.filter(data => data.type  == 'clear');
                this.blackOutTime = data;

            }, (err: any) => {
                console.log(err);
            });

    }

    public getblackOutTimesInt(date) {
        this.blackOutTimesService.getBlackOut(date).subscribe((data: any) => {
                this.blackOutTimelist = data;
            },
            (err: any) => {
                console.log(err);
            });
    }

    public dateStatus(dateStatusList) {
        let dateCalendatListDey: any;
        const dateCalendatList = this.elementRef.nativeElement.querySelectorAll('.ngb-dp-day .btn-light:not(.outside)');

        $('.ngb-dp-day .btn-light:not(.outside)').each(function (index) {

            $(this).addClass(dateStatusList[index].type);
        });

        dateCalendatList.forEach(function (eachObj) {
            dateCalendatListDey = eachObj.innerText;
        });
    }

    public addHtmlInline(dayTypeArry) {
        dayTypeArry.push(
            {
                'blackoutid': '',
                'date': '',
                'timeslotFrom': '',
                'timeslotTo': '',
                'textFrom': '',
                'textTo': '',
                'totalMinutes': ''
            }
        );
    }

    public getReservation(e) {
        this.success = false;
        this.editDataError = false;
        this.addDataError = false;
        const curentYear = $(e.currentTarget).find('.custom-select:last-child option:selected').text(),
            curentDayStrindSend = curentYear + '-' + $('.custom-select:first-child option:selected').val() + '-' + this.model.day;
        this.getCalendardataInit(curentDayStrindSend);
        this.getblackOutTimesInt(curentDayStrindSend);
        this.getDayTimeInit(curentDayStrindSend);
    }

    public deleteHtmlInline(index, dayTypeDelete, blackoutid) {
        dayTypeDelete.splice(index, 1);
        if (blackoutid){
            this.blackOutTimesService.daleteBlackOut(blackoutid).subscribe((data: any) => {
                },
                (err: any) => {
                    console.log(err);
                });
        }
    }

    public viewCorentDate() {
        this.currentMonthData = $('.custom-select:first-child option:selected').text() + ' ' + this.model.day;
    }

    public blackOutTimes(e) {
        this.success = false;
        this.editDataError = false;
        this.addDataError = false;

        e.preventDefault();
        const addBlackOutData: any = {
                'salonId': localStorage.stylistPlanerCurrentSalonId,
                'data': []
            },
            editBlackOutData: any = {
                'salonId': localStorage.stylistPlanerCurrentSalonId,
                'data': []
            };

        const curentDate: any = this.model.year + '-' + this.model.month + '-' + this.model.day;

        $('.black-out').each(function (i, obj) {


            if ($(obj).attr('data-blackoutid').length) {
                const datatimeslotTo: any = $(obj).find('.order-time-to option:selected').attr('data-timeslotTo'),
                    datatimeslotFrom: any = $(obj).find('.order-time-from option:selected').attr('data-timeslotFrom');


                editBlackOutData.data.push(
                    {
                        'blackoutid': $(obj).attr('data-blackoutid'),
                        'date': curentDate,
                        'fromId': datatimeslotFrom,
                        'toId': datatimeslotTo
                    }
                );

            } else {
                const datatimeslotTo: any = $(obj).find('.order-time-to option:selected').attr('data-timeslotTo'),
                    datatimeslotFrom: any = $(obj).find('.order-time-from option:selected').attr('data-timeslotFrom');



                addBlackOutData.data.push(
                    {
                        'date': curentDate,
                        'fromId': datatimeslotFrom,
                        'toId': datatimeslotTo
                    }
                );
            }
        });

        if (editBlackOutData.data.length !== 0) {
            this.blackOutTimesService.editBlackOut(editBlackOutData).subscribe((data: any) => {
                    const date: any = this.model.year + '-' + this.model.month + '-' + this.model.day;
                    this.getblackOutTimesInt(date);
                    this.getDayTimeInit(date);
                    this.success = data.result;
                },
                (err: any) => {
                    this.editDataError = err.error.message;
                });

        }
        if (addBlackOutData.data.length !== 0) {
            this.blackOutTimesService.putBlackOut(addBlackOutData).subscribe((data: any) => {
                    const date: any = this.model.year + '-' + this.model.month + '-' + this.model.day;
                    this.getblackOutTimesInt(date);
                    this.getDayTimeInit(date);
                    this.success = data.status;
                },
                (err: any) => {
                    this.addDataError = err.error.message;
                });
        }
        return false;
    }
}



