import {Component, OnInit} from '@angular/core';
import {ServicesService} from './services.service';
import {Router} from '@angular/router';

declare var $: any;

@Component({
    selector: 'app-services',
    templateUrl: './services.component.html',
    styleUrls: ['./services.component.scss']
})
export class ServicesComponent implements OnInit {

    servicesList: any;

    constructor(private servicesService: ServicesService, private router: Router) {
    }

    ngOnInit() {
        if (localStorage.getItem('stylistPlanerCurrentUserId') === null) {
            this.router.navigate(['sign-up']);
        }

        this.servicesService.getServices()
            .subscribe(
                (data: any) => {
                    this.servicesList = data;
                },
                err => {
                    console.log(err);
                });
    }

    editServiceItem(e) {
        this.servicesService.serviceId = $(e.target).closest('.service-item').find('td[data-serviceid]').attr('data-serviceid');
        this.servicesService.servicePrice = $(e.target).closest('.service-item').find('.service-price').text();
        this.servicesService.serviceDuration = $(e.target).closest('.service-item').find('.service-duration').text();
        this.servicesService.name = $(e.target).closest('.service-item').find('.service-name').text();

    }

}



