import {Component, OnInit} from '@angular/core';
import {ProfileService} from '../profile.service';
import {Router} from '@angular/router';
import {_countGroupLabelsBeforeOption} from '@angular/material';

@Component({
    selector: 'app-profile-edit',
    templateUrl: './profile-edit.component.html',
    styleUrls: ['./profile-edit.component.scss']
})
export class ProfileEditComponent implements OnInit {

    profileData: Array<any>;
    errorMessage: any;
    slylistLogin: any;
    userSalonMessengerId: any;
    userSalonPhone: any;
    adminLogin: any;

    constructor(private profileService: ProfileService, private router: Router) {
    }

    ngOnInit() {


        this.slylistLogin = localStorage.stylistPlanerCurrentRole == 2;

        this.adminLogin = localStorage.stylistPlanerCurrentRole == 1;

        this.profileService.getProfileData().subscribe((data: any) => {
                this.profileData = data.stylistProfile;
            },
            err => {
                console.log(err);
            });
    }

    public editStylistProfile(e) {
        e.preventDefault();
        const salonId: string = this.profileData[0].salonId,
            userPhone: number = e.target.elements[1].value,
            userFirstName: string = e.target.elements[2].value,
            userLastName: string = e.target.elements[3].value,
            userEmail: string = e.target.elements[4].value,
            userPass: string = e.target.elements[5].value,
            userSalonName: string = e.target.elements[6].value,
            userSalonEmail: string = e.target.elements[7].value;

        if (this.adminLogin) {
            this.userSalonMessengerId = e.target.elements[8].value;
            this.userSalonPhone = e.target.elements[8].value;
        }

        const profileEditData: any = {
            'salon': {
                'id': salonId,
                'name': userSalonName,
                'address': userSalonEmail,
                'phone':  this.userSalonPhone,
                'messengerId':  this.userSalonMessengerId

            },
            'stylist': {
                'firstName': userFirstName,
                'lastName': userLastName,
                'phone': userPhone,
                'email': userEmail,
                'password': userPass
            }
        };



        this.profileService.postProfileData(profileEditData).subscribe((data: any) => {
            this.router.navigate(['profile']);
        }, err => {
            this.errorMessage = err.error.message;

            console.log(err);
        });
        return false;
    }
}



