import {Component, OnInit} from '@angular/core';
import {RolesService} from './roles.service';
import {ProfileService} from '../profile/profile.service';
import {Router} from '@angular/router';

@Component({
    selector: 'app-roles',
    templateUrl: './roles.component.html',
    styleUrls: ['./roles.component.scss']
})
export class RolesComponent implements OnInit {

    stylistListUsers: any;

    constructor(private rolesService: RolesService, private profileService: ProfileService, private router: Router) {
    }

    ngOnInit() {
        this.rolesService.getListUsers().subscribe(
            (data: any) => {
                this.stylistListUsers = data;
            },
            (err: any) => {
                console.log(err);
            }
        );
    }

    public toProfile(userId, salonId, stylistRole) {
        this.profileService.userId = userId;
        this.profileService.salonId = salonId;
        this.profileService.stylistRole = stylistRole;
        this.router.navigate(['profile']);
    }
}



