import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs/Observable';
import {AppService} from '../app.service';

const httpOptions = {
    headers: new HttpHeaders({
        'x-api-key': '9UKaHgvVUu17sIVp0j5LF7tKqjvxQTIu1IkBUZ9y'
    })
};

@Injectable()
export class AdminReservationService {

    constructor(private http: HttpClient, private appService: AppService) {
    }

    getAvailabilityUrl = '/stylist-scheduling-service/api/orders';
    PIUrl: any = this.appService.globalApiUrl;


    getOrderList(): Observable<any> {
        return this.http.get(this.PIUrl + this.getAvailabilityUrl + '?userId=' + localStorage.stylistPlanerCurrentUserId, httpOptions);
    }


}



