import {Injectable} from '@angular/core';

import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs/Observable';
import {AppService} from '../app.service';


const httpOptions = {
    headers: new HttpHeaders({
        'x-api-key': '9UKaHgvVUu17sIVp0j5LF7tKqjvxQTIu1IkBUZ9y'
    })
};

@Injectable()
export class ServicesService {

    name: any;
    servicePrice: any;
    serviceDuration: any;
    serviceId: any;

    constructor(private http: HttpClient, private appService: AppService) {
    }

    PIUrl: any = this.appService.globalApiUrl;

    getServicesUrl = '/stylist-scheduling-service/api/users/services/';
    sendEditServiceUrl = '/stylist-scheduling-service/api/users/services/';
    putServicesUrl = '/stylist-scheduling-service/api/users/services/';

    getServices(): Observable<any> {
        return this.http.get(this.PIUrl + this.getServicesUrl + localStorage.stylistPlanerCurrentUserId, httpOptions);
    }


    sendEditService(data): Observable<any> {
        return this.http.post(this.PIUrl + this.sendEditServiceUrl + localStorage.stylistPlanerCurrentUserId  + '/' + this.serviceId, data, httpOptions);
    }

    deleteService() {
        return this.http.delete(this.PIUrl + this.sendEditServiceUrl + localStorage.stylistPlanerCurrentUserId  + '/' + this.serviceId, httpOptions);

    }

    putServices(data) {

        return this.http.put(this.PIUrl + this.putServicesUrl + localStorage.stylistPlanerCurrentUserId,  data,  httpOptions);
    }
}





