import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs/Observable';
import {AppService} from '../app.service';

const httpOptions = {
    headers: new HttpHeaders({
        'x-api-key': '9UKaHgvVUu17sIVp0j5LF7tKqjvxQTIu1IkBUZ9y'
    }),
};

@Injectable()
export class BlackOutTimesService {

    constructor(private http: HttpClient, private appService: AppService) {

    }
    PIUrl: any = this.appService.globalApiUrl;

    // getAvailabilityUrl = 'http://52.91.114.182/stylist-scheduling-service/api/blackout/view/';
    getBlackOutUrl = '/stylist-scheduling-service/api/blackout/view/';
    putBlackOutUrl = '/stylist-scheduling-service/api/blackout/add/';
    editBlackOutUrl = '/stylist-scheduling-service/api/blackout/edit/';
    daleteBlackOutUrl = '/stylist-scheduling-service/api/blackout/delete/';


    getBlackOut(date): Observable<any> {
        const getBlackOutOptions = {
            headers: new HttpHeaders({
                'x-api-key': '9UKaHgvVUu17sIVp0j5LF7tKqjvxQTIu1IkBUZ9y'
            }),

            params: {
                'dateFrom': date,
                'dateTo': date
            }
        };
        return this.http.get(this.PIUrl + this.getBlackOutUrl + localStorage.stylistPlanerCurrentUserId + '?salonId=' + localStorage.stylistPlanerCurrentSalonId, getBlackOutOptions);
    }

    editBlackOut(data): Observable<any> {
        return this.http.post(this.PIUrl + this.editBlackOutUrl + localStorage.stylistPlanerCurrentUserId, data, httpOptions);
    }

    putBlackOut(data): Observable<any> {
        return this.http.put(this.PIUrl + this.putBlackOutUrl + localStorage.stylistPlanerCurrentUserId, data, httpOptions);
    }


    daleteBlackOut(data): Observable<any> {
        return this.http.delete(this.PIUrl + this.daleteBlackOutUrl + localStorage.stylistPlanerCurrentUserId + '/' + data + '?salonId=' + localStorage.stylistPlanerCurrentSalonId, httpOptions);
    }

}




