import {Component, OnInit} from '@angular/core';
import {SignUpService} from './sign-up.service';
import {Router} from '@angular/router';

@Component({
    selector: 'app-sign-up',
    templateUrl: './sign-up.component.html',
    styleUrls: ['./sign-up.component.scss']
})
export class SignUpComponent implements OnInit {

    dataError: any;

    constructor(private signUpService: SignUpService, private router: Router) {
    }

    ngOnInit() {
        if (localStorage.stylistPlanerCurrentUserId &&
            localStorage.stylistPlanerCurrentSalonId) {
            this.router.navigate(['service']);
        }
    }
    /* Send user data for sign-up
    ------------------------------------*/
    public signUpUser(e) {
        e.preventDefault();
        const userPhone: number = e.target.elements[1].value,
            userFirstName: string = e.target.elements[2].value,
            userLastName: string = e.target.elements[3].value,
            userEmail: string = e.target.elements[4].value,
            userPass: string = e.target.elements[5].value,
            usersAlonName: string = e.target.elements[6].value,
            userSalonEmail: string = e.target.elements[7].value;
        const usersData: object = {
            'phone': userPhone,
            'firstName': userFirstName,
            'lastName': userLastName,
            'email': userEmail,
            'salonName': usersAlonName,
            'salonEmail': userSalonEmail,
            'password': userPass
        };
        this.signUpService.putUserData(usersData)
            .subscribe(
                (data: any) => {
                    this.router.navigate(['/sign-in']);
                },
                error => {
                    this.dataError = error.error.message;
                });
        return false;
    }
}




