
import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs/Observable';

import 'rxjs/add/operator/map';
import {AppService} from '../app.service';

const httpOptions = {
    headers: new HttpHeaders({
        'x-api-key': '9UKaHgvVUu17sIVp0j5LF7tKqjvxQTIu1IkBUZ9y'
    })
};

@Injectable()
export class RolesService {
    constructor(private http: HttpClient, private appService: AppService) {
    }
    PIUrl: any = this.appService.globalApiUrl;

    getListusersUrl = '/stylist-scheduling-service/api/users';


    getListUsers(): Observable<any> {
        return this.http.get(this.PIUrl + this.getListusersUrl , httpOptions);
    }
}



