import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs/Observable';
import {AppService} from '../app.service';

const httpOptions = {
    headers: new HttpHeaders({
        'x-api-key': '9UKaHgvVUu17sIVp0j5LF7tKqjvxQTIu1IkBUZ9y'
    })
};

@Injectable()
export class AvailabilityService {

    constructor(private http: HttpClient, private appService: AppService) {
    }

    // getAvailabilityUrl = 'http://52.91.114.182/stylist-scheduling-service/api/blackout/view/';
    getAvailabilityUrl = '/stylist-scheduling-service/api/availability/';
    putAvailabilityUrl = '/stylist-scheduling-service/api/availability/';
    PIUrl: any = this.appService.globalApiUrl;


    getAvailability(): Observable<any> {
        return this.http.get(this.PIUrl + this.getAvailabilityUrl + localStorage.stylistPlanerCurrentUserId + '?salonId=' + localStorage.stylistPlanerCurrentSalonId , httpOptions);
    }

    putAvailability(data): Observable<any> {
        return this.http.put(this.PIUrl + this.putAvailabilityUrl + localStorage.stylistPlanerCurrentUserId + '?salonId=' + localStorage.stylistPlanerCurrentSalonId , data, httpOptions);
    }

}



