import {Component, OnInit} from '@angular/core';
import {SignInService} from './sign-in.service';
import {Router} from '@angular/router';

@Component({
    selector: 'app-sign-in',
    templateUrl: './sign-in.component.html',
    styleUrls: ['./sign-in.component.scss']
})
export class SignInComponent implements OnInit {
    dataError: any;
    slylistLogin: any;
    adminLogin: any;

    constructor(private signInService: SignInService, private router: Router) {
    }

    ngOnInit() {
        if (localStorage.stylistPlanerCurrentUserId &&
            localStorage.stylistPlanerCurrentSalonId) {
            this.router.navigate(['/calendar']);
        }
    }

    /* Send user data for sign-up
  ------------------------------------*/
    public signInUser(e) {
        e.preventDefault();
        const userPhone: number = e.target.elements[1].value,
            userPassword: string = e.target.elements[2].value;
        const usersData: any = {
            'phone': userPhone,
            'password': userPassword
        };
        this.signInService.putSignInData(usersData)
            .subscribe(
                (data: any) => {
                    localStorage.setItem('stylistPlanerCurrentUserId', data.userId);
                    localStorage.setItem('stylistPlanerCurrentSalonId', data.salonId);
                    // localStorage.setItem('stylistPlanerCurrentRole', '1');
                    localStorage.setItem('stylistPlanerCurrentRole', data.role);
                    localStorage.setItem('stylistPlanerCurrentRoleName', data.roleName);


                    this.slylistLogin = localStorage.stylistPlanerCurrentRole == 2;
                    this.adminLogin = localStorage.stylistPlanerCurrentRole == 1;
                    if (this.slylistLogin) {
                        this.router.navigate(['/calendar']);

                    } else {
                        this.router.navigate(['/clients']);

                    }

                },
                error => {
                    console.log(error);
                    this.dataError = error.error.message;

                });
        return false;
    }

}



