import {Component, OnInit, DoCheck, PipeTransform, Pipe, Injectable} from '@angular/core';
import {Router} from '@angular/router';
import * as moment from 'moment';




@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit, DoCheck  {
    stylistPlanerCurrentUserId: boolean;

    constructor(private router: Router) {
    }



    ngDoCheck() {
        if (localStorage.getItem('stylistPlanerCurrentUserId') != null) {
            this.stylistPlanerCurrentUserId = true;
        } else {
            this.stylistPlanerCurrentUserId = false;
        }
    }

    ngOnInit () {

        if (localStorage.stylistPlanerCurrentRole == 2){
            this.router.navigate(['calendar']);
        }
        if (localStorage.stylistPlanerCurrentRole == 1){
            this.router.navigate(['clients']);
        }
        if (localStorage.getItem('stylistPlanerCurrentUserId') === null) {
            this.router.navigate(['sign-in']);
        }
    }
}

@Pipe({name: 'dateTimeFormatFilter'})
@Injectable()
export class DateTimeFormatPipe implements PipeTransform {
    transform(date: any, format: string): any {
        if (date) {
            return moment(date).format(format);

        }
    }
}

@Pipe({name: 'dateTimeFormatPipeTime'})
@Injectable()
export class DateTimeFormatPipeTime implements PipeTransform {
    transform(date: any, format: string): any {
        if (date) {
            return moment(date, 'HH:mm').format(format);
        }
    }
}



