import {Component, OnInit, ElementRef} from '@angular/core';
import {CalendarService} from '../calendar.service';
import {NgbDateStruct} from '@ng-bootstrap/ng-bootstrap';
import {NgbDateParserFormatter, NgbDatepickerConfig} from '@ng-bootstrap/ng-bootstrap';
import {Router} from '@angular/router';
import {DatePipe} from '@angular/common';
import {ServicesService} from '../../services/services.service';
import * as moment from 'moment';

declare var jquery: any;
declare var $: any;

const now = new Date();


@Component({
    selector: 'app-edit-appointment',
    templateUrl: './edit-appointment.component.html',
    styleUrls: ['./edit-appointment.component.scss']
})
export class EditAppointmentComponent implements OnInit {

    date: { year: number, month: number };
    model: NgbDateStruct;
    currentMonthData: string;
    clientName: string;
    duration: string;
    serviceList: string;
    serviceId: string;
    timeFrom: string;
    orderId: number;
    currentDayTime: any;
    errorMessage: any;
    currentDay: any;
    curentMonth: any;

    disabledAppointment: boolean;
    currentDayTimeClear: Array<any> = [];

    constructor(private calendarService: CalendarService,
                private servicesService: ServicesService,
                private parserFormatter: NgbDateParserFormatter,
                private elementRef: ElementRef,
                private configDate: NgbDatepickerConfig,
                private datePipe: DatePipe,
                private router: Router) {
        this.currentDay = this.calendarService.curentDay;
        this.curentMonth = this.calendarService.curentMonth;

        configDate.outsideDays = 'hidden';
        configDate.startDate = {year: now.getFullYear(), month: this.curentMonth};

        this.model = {
            year: now.getFullYear(), month: now.getMonth() + 1, day: this.currentDay === undefined ? now.getDate() : this.currentDay
        };
    }

    public viewAppointmentEditData(data) {
        this.serviceId = $(data.target).closest('.reservation-list').find('.service').attr('data-serviceId');
        this.clientName = $(data.target).closest('.reservation-list').find('.first-name').text();
        this.timeFrom = $(data.target).closest('.reservation-list').find('.time-from').text();
        this.duration = $(data.target).closest('.reservation-list').find('.time-from').attr('data-duration');
        this.orderId = $(data.target).closest('.reservation-list ').find('td').attr('data-orderId');
        this.calendarService.orderEditId = $(data.target).closest('.reservation-list ').find('td').attr('data-orderId');

    }

    ngOnInit() {

        if (this.calendarService.curentDay === undefined) {
            if (localStorage.stylistPlanerCurrentRole == 2) {
                this.router.navigate(['calendar']);
            }
        } else {
            this.servicesService.getServices()
                .subscribe(
                    (data: any) => {

                        this.serviceList = data;

                        if (this.calendarService.reservationListToEdit === undefined) {
                            this.router.navigate(['calendar']);
                        } else {
                            this.viewAppointmentEditData(this.calendarService.reservationListToEdit);
                        }
                    }
                );

            const cuentDateInit: any = this.currentDay === undefined ? now.getDate() : this.currentDay;
            const curentMonth: any = this.curentMonth === undefined ? this.model.month : this.curentMonth;

            this.model.day = this.calendarService.curentDay;
            this.getDayTimeInit(this.model.year + '-' + curentMonth + '-' + this.model.day);
            this.getCalendardataInit(this.model.year + '-' + curentMonth + '-' + this.model.day);
        }
    }


    public getCalendardataInit(date) {
        this.calendarService.getCalendardata(date)
            .subscribe(
                (data: any) => {
                    this.dateStatus(data);
                    this.viewCorentDate();
                    this.disabledAppointment = $('.bg-primary').hasClass('available') || $('.bg-primary').hasClass('filled');

                }
            );
    }

    public getDayTimeInit(date) {
        const orderIdEdit: any = $(this.calendarService.reservationListToEdit.target).closest('.reservation-list ').find('td').attr('data-orderId');
        this.calendarService.getDayTimeEdit(date, orderIdEdit)
            .subscribe(
                (data: any) => {
                    data = data.filter(data => data.type == 'clear');
                    // this.currentDayTime = data;
                    this.currentDayTime = data.filter(data => data.type == 'clear');

                    this.durationChange(data);

                }
            );
    }

    getDuration() {
        const curentMonth: any = this.curentMonth === undefined ? this.model.month : this.curentMonth;

        this.duration = $('.type-service option:selected').attr('data-duaration');
        this.getDayTimeInit(this.model.year + '-' + curentMonth + '-' + this.model.day);

    }

    durationChangeInit(event) {
        this.duration = $(event.target).val();
        const curentMonth: any = this.curentMonth === undefined ? this.model.month : this.curentMonth;

        this.getDayTimeInit(this.model.year + '-' + curentMonth + '-' + this.model.day);
    }


    public getReservation(e) {
        $(e.currentTarget).addClass('disabled');
        setTimeout(function () {
            $('ngb-datepicker').removeClass('disabled');
        }, 1000);


        const curentYear = $(e.currentTarget).find('.custom-select:last-child option:selected').text(),
            curentDayStrindSend = curentYear + '-' + $(e.currentTarget).find('.custom-select:first-child option:selected').val() + '-' + this.model.day;

        this.getDayTimeInit(curentDayStrindSend);
        this.getCalendardataInit(curentDayStrindSend);

        this.getCalendardataInit(curentDayStrindSend);
        this.getDayTimeInit(curentDayStrindSend);
    }


    public dateStatus(dateStatusList) {
        let dateCurentDey: any = this.model.day;

        let dateCalendatListDey: any;
        const dateCalendatList = this.elementRef.nativeElement.querySelectorAll('.ngb-dp-day .btn-light:not(.outside)');

        $('.ngb-dp-day .btn-light:not(.outside)').each(function (index) {
            $(this).removeClass('bg-primary text-white');

            if (index < dateStatusList.length) {
                $(this).addClass(dateStatusList[index].type);
            }
            if ($(this).text() == dateCurentDey) {
                $(this).addClass('bg-primary text-white');
            }
        });

        dateCalendatList.forEach(function (eachObj) {
            dateCalendatListDey = eachObj.innerText;
        });
    }

    public viewCorentDate() {
        this.currentMonthData = $('.custom-select:first-child option:selected').text() + ' ' + this.model.day;
    }


    getEditOrder() {
        const service: any = $('.type-service option:selected').val(),
            duration: number = $('.service-duration').val(),
            curentYear = $('.custom-select:last-child option:selected').text(),
            curentDayStrind = $('.custom-select:first-child option:selected').val() + '-' + this.model.day,
            curentDay = curentYear + '-' + curentDayStrind,
            dateFrom: any = curentDay + ' ' + $('.order-time option:selected').val();



        const profileEditData: any = {
            'dateFrom': moment(new Date(dateFrom)).format('YYYY-MM-DD HH:mm') ,
            'serviceId': service,
            'duration': duration
        };
        this.calendarService.postEditOrder(profileEditData).subscribe((data: any) => {
            if (localStorage.stylistPlanerCurrentRole == 2) {
                this.router.navigate(['calendar']);
            }
        }, err => {
            this.errorMessage = err.error.message;
            console.log(err);
        });

        this.calendarService.curentMonth = parseInt($('.custom-select:first-child option:selected').val(), 10);
        this.calendarService.curentDay = this.model.day;

    }

    public deleteCurrentOrder() {
        this.calendarService.deleteOrder(this.orderId).subscribe(data => {
            if (localStorage.stylistPlanerCurrentRole == 2) {
                this.router.navigate(['calendar']);
            }
        }, error => {

        });
    }


    public durationChange(date) {
        const curentMonth: any = this.curentMonth === undefined ? this.model.month : this.curentMonth;
        const curentDayStrindSend = this.model.year + '-' + curentMonth + '-' + this.model.day;
        if (this.duration) {
            this.calendarService.getClearTimeEdit(curentDayStrindSend, this.duration, this.orderId).subscribe(
                (data: any) => {
                    const currentDayTimeClearArr = [];
                    $.each(data, function (i, dataObj) {

                        $.each(date, function (i2, currentDayTimeObj) {
                            if (currentDayTimeObj.timeslot <= dataObj.timeslot_to && currentDayTimeObj.timeslot >= dataObj.timeslot_from) {
                                currentDayTimeClearArr.push(this);
                            }
                        });
                    });
                    this.currentDayTimeClear = currentDayTimeClearArr;
                }, (err: any) => {
                    console.log(err);
                }
            );

        }
    }
}



