import {Component, OnInit} from '@angular/core';
import {ProfileService} from '../profile.service';
import {RolesService} from '../../roles/roles.service';
import {Router} from '@angular/router';

declare var $: any;

@Component({
    selector: 'app-profile-edit-admin',
    templateUrl: './profile-edit-admin.component.html',
    styleUrls: ['./profile-edit-admin.component.scss']
})
export class ProfileEditAdminComponent implements OnInit {
    profileData: Array<any>;
    stylistListUsers: any;
    curentStylist: any;
    salonPhone: any;
    success: any;
    editDataError: any;
    stylistRole: any;

    constructor(private profileService: ProfileService,
                private rolesService: RolesService,
                private router: Router) {
    }

    ngOnInit() {
        this.stylistRole = this.profileService.stylistRole;
        this.curentStylist = this.profileService.userId === undefined ? localStorage.stylistPlanerCurrentUserId : this.profileService.userId;
        if (this.profileService.userId === undefined) {
            this.router.navigate(['roles']);
        }

        this.rolesService.getListUsers().subscribe(
            (data: any) => {

                this.stylistListUsers = data;
            },
            (err: any) => {
                console.log(err);
            }
        );

        this.profileService.getProfileData().subscribe((data: any) => {
                this.profileData = data.stylistProfile;
            },
            err => {
                console.log(err);
            });
    }


    public sendAdminEditProfile(e) {
        e.stopPropagation();

        const userSalonPhone: string = e.target.elements[0].value,
            salonName = $('.salonName').text(),
            salonAddress = $('.salonAddress').text(),
            userSalonMessengerId: number = e.target.elements[1].value,
            userStylistRole: number = e.target.elements[2].value,

            stylistFirstName = $('.stylistFirstName').text(),
            stylistLastName = $('.stylistLastName').text(),
            stylistPhone = $('.stylistPhone').text(),
            stylistEmail = $('.stylistEmail').text();

        const profileEditData: any = {
            'salon': {
                'name': salonName,
                'id': localStorage.stylistPlanerCurrentSalonId,
                'phone': userSalonPhone,
                'messengerId': userSalonMessengerId,
                'address': salonAddress,


            },
            'stylist': {
                'firstName': stylistLastName,
                'lastName': stylistFirstName,
                'phone': stylistPhone,
                'email': stylistEmail,
                'role': userStylistRole,
            }
        };


        this.profileService.postAdminProfileData(profileEditData).subscribe(
            (data: any) => {
                this.success = data.status;
            },
            (err: any) => {
                this.editDataError = err.error.message;

            }
        );
        return false;
    }
}



