import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs/Observable';

import 'rxjs/add/operator/map';
import {AppService} from '../app.service';

const httpOptions = {
    headers: new HttpHeaders({
        'x-api-key': '9UKaHgvVUu17sIVp0j5LF7tKqjvxQTIu1IkBUZ9y'
    })
};

@Injectable()
export class ProfileService {
    constructor(private http: HttpClient, private appService: AppService) {
    }

    PIUrl: any = this.appService.globalApiUrl;
    userId: any;
    salonId: any;
    stylistRole: any;
    getProfileUrl = '/stylist-scheduling-service/api/users/profile/';
    postProfileUrl = '/stylist-scheduling-service/api/users/edit/';
    postAdminProfileDataUrl = '/stylist-scheduling-service/api/users/edit/';

    getProfileData(): Observable<any> {
        const userIdToUrl = this.userId !== undefined ? this.userId : localStorage.stylistPlanerCurrentUserId;
        const salonIdToUrl = this.salonId !== undefined ? this.userId : localStorage.stylistPlanerCurrentSalonId;
        return this.http.get(this.PIUrl + this.getProfileUrl + userIdToUrl, httpOptions)
            .map((data: any) => {
                const dataSalon = data.salon,
                    dataStylist = data.stylist;
                return {
                    'stylistProfile': [
                        {
                            'salonId': dataSalon.id,
                            'salonName': dataSalon.name,
                            'salonAddress': dataSalon.address,
                            'salonMessengerId': dataSalon.messengerId,
                            'salonPhone': dataSalon.phone,
                            'stylistEmail': dataStylist.email,
                            'stylistFirstName': dataStylist.firstName,
                            'stylistLastName': dataStylist.lastName,
                            'stylistPhone': dataStylist.phone
                        }
                    ],
                };
            });
    }

    postProfileData(profileEditData): Observable<any> {
        return this.http.post(this.PIUrl + this.postProfileUrl + localStorage.stylistPlanerCurrentUserId, profileEditData, httpOptions);
    }

    postAdminProfileData(profileEditData): Observable<any> {

        return this.http.post(this.PIUrl + this.postAdminProfileDataUrl + localStorage.stylistPlanerCurrentUserId, profileEditData, httpOptions);
    }
}



