import {Component, OnInit} from '@angular/core';
import {AvailabilityService} from './availability.service';
import {CalendarService} from '../calendar/calendar.service';
import {DatePipe} from '@angular/common';

declare var $: any;
const now = new Date();

@Component({
    selector: 'app-availability',
    templateUrl: './availability.component.html',
    styleUrls: ['./availability.component.scss']
})
export class AvailabilityComponent implements OnInit {


    availabilityListFriday: any;
    availabilityListMonday: any;
    availabilityListSaturday: any;
    availabilityListSunday: any;
    availabilityListThursday: any;
    availabilityListTuesday: any;
    availabilityListWednesday: any;
    availabilitytime: any;
    availabilityDataPut: any;
    dataError: any;
    success: any;
    date: any;

    constructor(private availabilityService: AvailabilityService, private calendarService: CalendarService, private datePipe: DatePipe) {
    }

    ngOnInit() {
        this.availabilityService.getAvailability()
            .subscribe((data: any) => {
                this.availabilityListFriday = data.week.friday;
                this.availabilityListMonday = data.week.monday;
                this.availabilityListSaturday = data.week.saturday;
                this.availabilityListSunday = data.week.sunday;
                this.availabilityListThursday = data.week.thursday;
                this.availabilityListTuesday = data.week.tuesday;
                this.availabilityListWednesday = data.week.wednesday;
            }, (err: any) => {
                console.log(err);
            });
        const dateFrom: any = this.datePipe.transform(now, 'yyyy-MM-dd');

        this.calendarService.getDayTime(dateFrom)
            .subscribe((data: any) => {
                // data = data.filter(data => data.type  == 'clear');

                this.availabilitytime = data;

            }, (err: any) => {
                this.dataError = err.error.message;

                console.log(err);
            });
    }


    addHtmlInline(dayTypeArry) {
        dayTypeArry.push(
            {
                'timeslotFrom': '',
                'timeslotTo': '',
                'textFrom': '',
                'textTo': ''
            }
        );
    }

    deleteHtmlInline(index, dayTypeDelete) {
        dayTypeDelete.splice(index, 1);
    }


    saveAvailAbility() {
        this.success = false;
        this.dataError = false;

        const monday: any = this.availabilityListMonday,
            tuesday: any = this.availabilityListTuesday,
            wednesday: any = this.availabilityListWednesday,
            thursday: any = this.availabilityListThursday,
            friday: any = this.availabilityListFriday,
            saturday: any = this.availabilityListSaturday,
            sunday: any = this.availabilityListSunday;

        this.availabilityDataPut = {
            'salonId': localStorage.stylistPlanerCurrentSalonId,
            week: {
                monday,
                tuesday,
                wednesday,
                thursday,
                friday,
                saturday,
                sunday,
            }
        };

        $('.sunday .order-time-from option:selected').each(function (i) {

            sunday[i].textFrom = $(this).val();
            sunday[i].timeslotFrom = $(this).attr('data-timeslotFrom');
        });
        $('.tuesday .order-time-from option:selected').each(function (i) {
            tuesday[i].textFrom = $(this).val();
            tuesday[i].timeslotFrom = $(this).attr('data-timeslotFrom');

        });
        $('.wednesday .order-time-from option:selected').each(function (i) {
            wednesday[i].textFrom = $(this).val();
            wednesday[i].timeslotFrom = $(this).attr('data-timeslotFrom');

        });
        $('.thursday .order-time-from option:selected').each(function (i) {
            thursday[i].textFrom = $(this).val();
            thursday[i].timeslotFrom = $(this).attr('data-timeslotFrom');

        });
        $('.friday .order-time-from option:selected').each(function (i) {
            friday[i].textFrom = $(this).val();
            friday[i].timeslotFrom = $(this).attr('data-timeslotFrom');

        });
        $('.saturday .order-time-from option:selected').each(function (i) {
            saturday[i].textFrom = $(this).val();
            saturday[i].timeslotFrom = $(this).attr('data-timeslotFrom');

        });
        $('.monday .order-time-from option:selected').each(function (i) {
            monday[i].textFrom = $(this).val();
            monday[i].timeslotFrom = $(this).attr('data-timeslotFrom');
        });


        $('.sunday .order-time-to option:selected').each(function (i) {
            sunday[i].timeslotTo = $(this).attr('data-timeslotTo');
            sunday[i].textTo = $(this).val();
        });
        $('.tuesday .order-time-to option:selected').each(function (i) {
            tuesday[i].textTo = $(this).val();
            tuesday[i].timeslotTo = $(this).attr('data-timeslotTo');

        });
        $('.wednesday .order-time-to option:selected').each(function (i) {
            wednesday[i].textTo = $(this).val();
            wednesday[i].timeslotTo = $(this).attr('data-timeslotTo');

        });
        $('.thursday .order-time-to option:selected').each(function (i) {
            thursday[i].textTo = $(this).val();
            thursday[i].timeslotTo = $(this).attr('data-timeslotTo');

        });
        $('.friday .order-time-to option:selected').each(function (i) {
            friday[i].textTo = $(this).val();
            friday[i].timeslotTo = $(this).attr('data-timeslotTo');

        });
        $('.saturday .order-time-to option:selected').each(function (i) {
            saturday[i].textTo = $(this).val();
            saturday[i].timeslotTo = $(this).attr('data-timeslotTo');

        });
        $('.monday .order-time-to option:selected').each(function (i) {
            monday[i].textTo = $(this).val();
            monday[i].timeslotTo = $(this).attr('data-timeslotTo');
        });
    }

    sendDataAvailability() {
        this.availabilityService.putAvailability(this.availabilityDataPut).subscribe((data: any) => {
            this.success = data.result;
        }, (err: any) => {
            this.dataError = err.error.message;
            console.log(err);
        });
    }
}



