import {Component, OnInit} from '@angular/core';
import {ProfileService} from '../profile.service';

@Component({
    selector: 'app-profile-admin',
    templateUrl: './profile-admin.component.html',
    styleUrls: ['./profile-admin.component.scss']
})
export class ProfileAdminComponent implements OnInit {

    profileData: Array<any>;
    slylistLogin: any;
    adminLogin: any;
    editStylist: any;
    editAdmin: any;

    constructor(private profileService: ProfileService) {
    }

    ngOnInit() {
        this.slylistLogin = localStorage.stylistPlanerCurrentRole == 2;
        this.adminLogin = localStorage.stylistPlanerCurrentRole == 1;
        this.editStylist = this.profileService.userId;
        this.editAdmin = this.editStylist === undefined;

        this.profileService.getProfileData().subscribe((data: any) => {
                this.profileData = data.stylistProfile;
            },
            err => {
                console.log(err);
            });


    }
}










