import {Component, OnInit} from '@angular/core';
import {Router} from '@angular/router';
import {ClientsService} from './clients.service';


declare var $: any;

@Component({
    selector: 'app-clients',
    templateUrl: './clients.component.html',
    styleUrls: ['./clients.component.scss']
})
export class ClientsComponent implements OnInit {

    customersList: Array<any> = [];
    slylistLogin: any;
    adminLogin: any;

    constructor(private clientsService: ClientsService, private router: Router) {
    }

    ngOnInit() {

        this.slylistLogin = localStorage.stylistPlanerCurrentRole == 2;
        this.adminLogin = localStorage.stylistPlanerCurrentRole == 1;

        if ( this.adminLogin){
            this.clientsService.getClientsListAll().subscribe((data: any) => {
                this.customersList = data;
            });
        }else{
            this.clientsService.getClientsList().subscribe((data: any) => {
                this.customersList = data;
            });
        }

    }

   public detailsClients (customerId, nextAppointment, serviceName) {

       this.clientsService.nextAppointment = nextAppointment;
       this.clientsService.serviceName = serviceName;
       this.clientsService.customerId = customerId;
       this.router.navigate(['clients/details']);
    }
}




