import { Component, OnInit } from '@angular/core';
import {Router} from '@angular/router';

@Component({
  selector: 'app-thank-sign-up',
  templateUrl: './thank-sign-up.component.html',
  styleUrls: ['./thank-sign-up.component.scss']
})
export class ThankSignUpComponent implements OnInit {

  constructor(private router: Router) { }

  ngOnInit() {
      if (localStorage.stylistPlanerCurrentUserId &&
          localStorage.stylistPlanerCurrentSalonId) {
          this.router.navigate(['service']);
      }
  }

}



